import { useState, useRef, useEffect } from "react";
import Post from "./Post";
import { FaArrowCircleRight, FaArrowCircleLeft } from "react-icons/fa";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";

const Carousel = ({ data, loading }) => {
  const maxScrollWidth = useRef(0);
  const [currentIndex, setCurrentIndex] = useState(0);
  const carousel = useRef(null);

  const movePrev = () => {
    if (currentIndex > 0) {
      setCurrentIndex((prevState) => prevState - 1);
    }
  };

  const moveNext = () => {
    if (
      carousel.current !== null &&
      carousel.current.offsetWidth * currentIndex <= maxScrollWidth.current
    ) {
      setCurrentIndex((prevState) => prevState + 1);
    }
  };

  const isDisabled = (direction) => {
    if (direction === "prev") {
      return currentIndex <= 0;
    }

    if (direction === "next" && carousel.current !== null) {
      return (
        carousel.current.offsetWidth * currentIndex >= maxScrollWidth.current
      );
    }

    return false;
  };

  useEffect(() => {
    if (carousel !== null && carousel.current !== null) {
      carousel.current.scrollLeft = carousel.current.offsetWidth * currentIndex;
    }
  }, [currentIndex]);

  useEffect(() => {
    maxScrollWidth.current = carousel.current
      ? carousel.current.scrollWidth - carousel.current.offsetWidth
      : 0;
  }, []);

  return (
    <div className="carousel relative ml-[50px] md:ml-[100px] lg:ml-[160px] ">
      <div className="pt-5 w-fit relative">
        <span className="border-b-[8px] border-orange-400/20 top-2/3 absolute dark:border-purple-800/40 w-full "></span>
        <span className="relative font-medium text-gray-800 text-3xl text-left  dark:text-white">
          RECENT BLOGPOSTS
        </span>
      </div>
      <div className="pb-20 relative overflow-hidden">
        <div className="flex justify-between items-center absolute top left w-full h-full">
          <button
            onClick={movePrev}
            className=" text-white w-fit text-center  hover:opacity-100 disabled:opacity-0 z-10 p-0 m-0 transition-all ease-in-out duration-300"
            disabled={isDisabled("prev")}
          >
            <FaArrowCircleLeft
              className="text-blue-900 dark:text-white"
              size={30}
            />
            <span className="sr-only">Prev</span>
          </button>
          <button
            onClick={moveNext}
            className=" text-white w-fit text-center hover:opacity-100 disabled:opacity-0 z-10 p-0 m-0 transition-all ease-in-out duration-300"
            disabled={isDisabled("next")}
          >
            <FaArrowCircleRight
              size={30}
              className="text-blue-900 dark:text-white"
            />
            <span className="sr-only">Next</span>
          </button>
        </div>
        <div
          ref={carousel}
          className="carousel-container py-9 relative flex gap-8 lg:gap-10  overflow-hidden scroll-smooth snap-x snap-mandatory touch-pan-x z-0"
        >
          {!loading
            ? data?.map((data, i) => (
                <div
                  key={i}
                  className="relative carousel-item snap-start text-center "
                >
                  <Post blog={data} />
                </div>
              ))
            : [...Array(5)].map((val, i) => (
                <div
                  key={i}
                  className="relative carousel-item snap-start"
                >
                  <div
                    key={i}
                    className="flex flex-col w-[280px] md:w-[290px] shadow-box shadow-black/20 hover:shadow-black/50 rounded-3xl p-[9px] overflow-hidden dark:bg-formBox bg-back ring-2 ring-opacity-5 ring-black"
                  >
                    <div className="h-[160px] w-full md:h-[180px] dark:border-background dark:bg-formBox">
                      <Skeleton height={160} className="" baseColor="gray" />
                    </div>
                    <div className="pt-2 pb-4 px-4 flex flex-col">
                      <span>
                        <Skeleton width={140} className="" baseColor="silver" />
                      </span>
                      <div className="flex flex-col mt-2 gap-1">
                        <span>
                          <Skeleton width={100} className="" baseColor="gray" />
                        </span>
                        <span className="flex flex-col">
                          <Skeleton height={12} baseColor="silver" />
                          <Skeleton height={12} baseColor="silver" />
                        </span>
                      </div>
                      <span className="flex justify-end mt-2">
                        <Skeleton width={80} height={30} baseColor="silver" />
                      </span>
                    </div>
                  </div>
                </div>
              ))}
        </div>
      </div>
    </div>
  );
};

export default Carousel;
